*{
  margin: 0;
  padding: 0;
}

body{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}

ul{
  margin: 10;
  padding: 50;
}

ul li{
  list-style: none;
}

a{
  text-decoration: none;
}
.chatCointainer {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  width: 100%;
  height: calc(100% - 60px);
  overflow: hidden;
}
span {
  color: white;
  font-family: 'Raleway', sans-serif;
}
.listOfUsers {
  width: 30%;
  height: 100%;
  overflow-x: hidden;
  border-top: 1px solid #ccc;
  background-color: #1b5ff9;
}

.singleUserElement {
  background-color: #1b5ff9;
  border-bottom: 0.5px solid #ccc;
}

.singleUserElement:hover {
  background-color: #1946db;
}

.chatArea {
  width: 70%;
  height: 100%;
  overflow-x: hidden;
  position: relative;
}
.chatHeader {
  position: fixed;
  width: 70%;
  height: 40px;
  background: #e2e0e0;
  text-align: center;
  line-height: 40px;
  color: #000;
  font-weight: bold;
  font-family: sans-serif;
}

.chatControls {
  display: -webkit-flex;
  display: flex;
  position: fixed;
  width: 70%;
  height: 50px;
  bottom: 0;
}

.chatControls textarea {
  width: 90%;
}

.chatControls button {
  width: 10%;
}

.displayName {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 5px 10px;
  box-sizing: border-box;
  cursor: pointer;
}

.displayName.active {
  background: #ccc;
}

.displayName .displayPic {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 25px;
}

.displayName .displayPic img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.messageStyle {
  font-size: 18px;
  font-weight: 500;
  position: relative;
  display: inline-block;
  padding: 5px 10px;
  border-radius: 10px;
  margin: 5px;
  border-radius: 20px 20px 20px 20px;
}

.messageSections {
  height: calc(100% - 90px);
  width: 100%;
  overflow-x: hidden;
  position: relative;
  top: 40px;
}

.onlineStatus {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #16e316;
  border-radius: 5px;
  box-shadow: 0 0 10px 0 #16e316;
}
.onlineStatus.off {
  background: green;
  box-shadow: 0 0 0 0;
}

.messageBubble {
  font-family: arial, sans-serif;
  font-size: 1.1em;
  color: #fff;
  position: relative;
  background: #a53d38;
  padding: 20px;
  border-radius: 10px;
}

.sendMessageInput {
  display: grid;
  width: 85%;
  height: 85%;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 15px;
  border-radius: 20px 20px 20px 20px;
  border-top: 1px solid rgba(0, 0, 0, 25);
  margin-bottom: 6px;
  margin-right: 50px;
  padding: 10px;  
  outline: none;
}

.sendMessageBtn {
  width: 10%;
  height: 85%;
  background: #f3f0f1;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 300px;
  border-color: rgba(0, 0, 0, 25);
  border: solid black 2px;
  text-align: center;
  cursor: pointer;
  outline: none;
}

.sendMessageBtn:hover {
  cursor: pointer;
  background-color: #ccc;
}

.deleteMessageBtn {
  width: 30px;
  height: 30px;
  position: relative;
  margin: -7px;
  outline: none;
}
.header{
    width: 100%;
    position: relative;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    background: #1b5ff9;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    box-sizing: border-box;
}

.logo{
    font-size: 20px;
    color: #fff;
    text-transform: capitalize;
    font-weight: bold;
    margin: 18px;
    text-shadow: 0 0;
}

.menu{
    display: -webkit-flex;
    display: flex;
    margin: 20px;
}

.menu > li > a{
    color: #fff;
    font-weight: bold;
    text-shadow: 0 0;
    text-decoration: none;
}

.leftMenu{
    display: -webkit-flex;
    display: flex; 
    color: #fff; 
    margin: 20px;
}

.leftMenu > li{
    margin: 0 10px;
}

.leftMenu > li > a{
    color: #fff;
}

.leftMenu > li > a.active{
    font-weight: bold;
}

.displayUserPic {
    width: 50px;
    height: 50px;
    overflow: hidden;
    border-radius: 25px;
    object-fit: cover;
    display: inline-block;
  }
